<template>
	<div class="box">
		<div class="card">
			<div class="cardTop">
				<img src="../../../assets/guideLogo.png" />
				<div><span>DeFi</span> Era</div>
			</div>
			<div class="cardContent">
				<div class="title">{{list.title}}</div>
				<p>{{list.content}}</p>
				<div class="bottom">
					<span>{{list.from}}</span>
					<span>{{list.time}}</span>
				</div>
			</div>
			<div class="cardBottom">
				<div>
					<div>{{$t('HomeText09')}}</div>
					<span>https://defiera.gitee.io</span>
					<span>https://defiera.github.io</span>
				</div>
				<div class="qrcode">
					<div></div>
				</div>
			</div>
		</div>
		
		<button @click="save">{{$t('Screenshot')}}</button>
	</div>
</template>

<script>
	import QRCode from 'qrcodejs2'
	import html2canvas from 'html2canvas'
	export default{
		components:{ QRCode },
		data(){
			return {
				list: {},
			}
		},
		methods:{
			save() {
				const a = document.createElement('a')
				a.download = 'message'
				html2canvas(document.querySelector('.card')).then(canvas => {
					a.href = canvas.toDataURL('image/png')
					a.click()
				})
			},
		},
		mounted(){
			this.list = JSON.parse(localStorage.getItem('message'))
			
			const qrcode = document.querySelector('.qrcode>div')
			new QRCode(qrcode, 'https://defiera.zendesk.com')
			qrcode.children.forEach(item => {
				item.style.width = '100%'
				item.style.height = '100%'
			})
		},
	}
</script>

<style lang="scss" scoped>
	.box{
		padding: 44px 26px 0;
		display: flex;
		flex-direction: column;
	}
	.cardTop{
		padding: 78px 0 56px 40px;
		display: flex;
		align-items: center;
		background-color: #293e59;
		background-image: url(../../../assets/messageCardBg.png);
		background-repeat: no-repeat;
		background-size: 700px 372px;
		
		&>img{
			width: 114px;
			height: 129px;
			margin-right: 16px;
		}
		
		&>div{
			font-size: 70px;
			
			&>span{
				color: #21C17A;
			}
		}
	}
	
	.cardContent{
		padding: 32px 56px 20px 44px;
		font-size: 32px;
		color: #000;
		background-color: #E5E9FF;
		text-align: left;
		line-height: 1.5;
		border-bottom: 2px solid #000000;
		
		&>p{
			margin: 34px 0 276px;
			font-size: 28px;
			color: #18222F;
		}
		
		&>.bottom{
			display: flex;
			flex-direction: column;
			font-size: 24px;
			color: #18222F;
		}
	}
	
	.cardBottom{
		background-color: #E5E9FF;
		display: flex;
		justify-content: space-between;
		align-items: center;
		padding: 36px 56px 36px 44px;
		
		&>div:nth-child(1){
			color: #000;
			font-size: 24px;
			display: flex;
			flex-direction: column;
			align-items: flex-start;
			text-align: left;
			
			&>div{
				font-size: 32px;
				margin-bottom: 14px;
			}
		}
		
		&>.qrcode{
			width: 139px;
			height: 139px;
		}
	}
	
	button{
		width: 300px;
		height: 80px;
		color: #fff;
		border: none;
		border-radius: 50px;
		font-size: 32px;
		background: linear-gradient(44deg,rgba(0,209,160,1) 0%,#21C1A8 100%);
		line-height: 80px;
		margin: 40px auto 0;
	}
</style>
