<template>
	<div>
		<nav-bar :title="title"></nav-bar>
		<div class="navBox">
			<div class="content">
				<div class="line"></div>
				<div class="list" v-for="(item,index) in list" :key="index">
					<span>{{item.time}}</span>
					<div class="title">{{item.title}}</div>
					<p>{{item.content}}</p>
					<div class="listBottom">
						<span>{{item.from}}</span>
						<div @click="pushMessageCard(item)">
							<img src="../../../assets/share.png" />
							<span>{{$t('Share')}}</span>
						</div>
					</div>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
	import navBar from '@/components/nav-bar'
	export default {
		components: { navBar },
		data(){
			return {
				title: this.$t('News'),
				list: [
					{
						title: this.$t('title1'),
						content: this.$t('content1'),
						time: '2020/06/06 15:00',
						from: 'DF.R Team'
					},
					{
						title: this.$t('title2'),
						content: this.$t('content2'),
						time: '2020/06/01 12:00',
						from: 'DF.R Team'
					},
				]
			}
		},
		methods:{
			pushMessageCard(item){
				localStorage.setItem('message',JSON.stringify(item))
				this.$router.push('./messageCard')
			}
		}
	}
</script>

<style lang="scss" scoped>
	.navBox{
		background-color: #273446!important;
		
		.content{
			padding: 30px 60px 0 70px;
			position: relative;
			
			.line{
				position: absolute;
				width: 0;
				height: 100%;
				border-right: 2px solid #516A89;
				left: 36px;
				top: 0;
			}
			
			.list:last-child{
				margin-bottom: 0!important;
			}
			
			.list{
				font-size: 28px;
				text-align: left;
				margin-bottom: 60px;
				
				&>span{
					color: #516A89;
					position: relative;
				}
				
				&>span::after{
					content: '';
					width: 22px;
					height: 22px;
					position: absolute;
					border-radius: 50%;
					background-color: #21C17A;
					left: -44px;
					top: 50%;
					transform: translateY(-50%);
				}
				
				&>.title{
					font-size: 32px;
					color: #fff;
					margin: 18px 0 12px;
				}
				
				&>p{
					color: #A1BDE1;
				}
				
				&>.listBottom{
					display: flex;
					justify-content: space-between;
					margin-top: 32px;
					color: #516A89;
					
					&>span{
						font-size: 24px;
					}
					
					&>div{
						display: flex;
						align-items: center;
						line-height: 1.2;
						
						&>img{
							width: 33px;
							height: 34px;
							margin-right: 18px;
						}
					}
				}
			}
		}
	}
</style>
